@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;500;600;700&display=swap);
header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.container h5 {
  font-size: 0rem;
}
.container h5 {
  font-size: 3rem;
}

.container h1 {
  font-weight: 1000;
  color: transparent;
  -webkit-text-stroke: 1px rgb(57, 175, 175);
  background: url(data:image/png;base64,);
  -webkit-background-clip: text;
  background-position: 0 0;
  -webkit-animation: back 20s linear infinite;
          animation: back 20s linear infinite;
}

@-webkit-keyframes back {
  100% {
    background-position: 2000px 0;
  }
}

@keyframes back {
  100% {
    background-position: 2000px 0;
  }
}

.cta {
  margin-top: 1rem;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
}

.cta .btn {
  height: 45px;
}

.header_socials {
  font-size: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  position: absolute;
  left: 0 rem;
  bottom: 3rem;
}

.header_socials::after {
  content: "";
  width: 1px;
  height: 2 rem;
  background-color: var(--color-primary);
}

.me {
  background-image: linear-gradient(transparent, var(--color-primary));
  width: 20rem;
  height: auto;
  position: relative;
  left: calc(50% - 10rem);
  margin-top: 2rem;
  border-radius: 200px 200px 0 0;
}
.me:hover {
  background-image: linear-gradient(rgb(19, 85, 69), transparent);
  cursor: pointer;
}

img {
  margin-top: 1rem;
  border-radius: 100px 100px 0 0;
}

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 8rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 200;
  font-size: 1.3rem;
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 1024px) {
  header {
    margin-top: 2rem;
    height: 70vh;
  }
  .container h1 {
    font-size: 4rem;
  }
  .container h5 {
    font-size: 2rem;
  }
  .header_socials,
  .scroll__down {
    display: none;
  }
  .me {
    width: 10rem;
    height: 17rem;
    left: calc(50% - 6rem);
    margin-top: 4rem;
    border-radius: 50px 50px 0 0;
  }
  img {
    margin-top: 4rem;
    /* border-radius: 100px 100px 0 0; */
  }
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 600px) {
  header {
    margin-top: 2rem;
    height: 80vh;
  }

  .container h1 {
    font-size: 2rem;
  }
  .container h5 {
    font-size: 1rem;
  }

  .container img {
    margin-top: 1rem;
  }
  .me {
    background-image: none;
    width: 10rem;
    height: 16rem;
    left: calc(50% - 5.3rem);
    margin-top: 1rem;
    border-radius: 200px 200px 0 0;
  }

  .header_socials,
  .scroll__down {
    display: none;
  }
}

nav {
  background: rgba(0, 0, 0, 0.4);
  width: -webkit-max-content;
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

nav a {
  background-color: transparent;
  padding: 0.5rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.4rem;
}

nav a:hover {
  background-color: var(--color-bg);
}

