  background: var(--color-bg);
  color: var(--color-white);
} */

#about {
  padding-top: 4rem;
}
#about h2 {
  font-size: 4rem;
  color: var(--color-white);
}
#about h5 {
  font-size: 2rem;
}
.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.about__me {
  margin-top: 2rem;
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    transparent);
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary, transparent)
  );
  display: grid;
  place-items: center;
}

img {
  margin-top: 0;
  width: auto;
  height: auto;
  border-radius: 2rem;
}

.about_me-image {
  overflow: hidden;
  -webkit-transform: rotate(15deg);
          transform: rotate(15deg);
  transition: all 600ms ease;
}

.about_me-image:hover {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

.about__content p {
  font-size: 1.2rem;
  margin: 5rem 0 2.3rem;
  color: var(--color-light);
}
@media screen and (max-width: 1024px) {
  #about {
    margin-top: 2rem;
    height: 70vh;
  }
  .about__container {
    margin-top: 3rem;
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
  }
  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about__content p {
    font-size: 1.2rem;
    margin: 1rem 0.4rem 1.5rem;
  }
}

@media screen and(max-width:1000px) {
  #about {
    height: 20rem;
  }
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  img {
    margin: 0px;
  }
  .about__content {
    height: 40vh;
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 1.2rem;
  }
}

.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

#experience {
  padding-top: 4rem;
  padding-bottom: 10rem;
}

section h1 {
  text-align: center;
}

.experience__container > div {
  background: var(--color-bg-variant);
  padding: 2.4rem 3rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.experience__container > div:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-white);
}

.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.experience__details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.experience__details-icon {
  margin-top: 6px;
  color: var(--color-primary);
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 1024px) {
  .experience__container {
    grid-template-columns: 1fr;
  }

  .experience__container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience__content {
    padding: 1rem;
  }
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 600px) {
  #experience {
    margin-top: 30rem;
  }
  .experience__container {
    grid-gap: 1rem;
    gap: 1rem;
  }
  .experience__container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}

.portfolio__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}
#portfolio {
  padding-top: 3rem;
  padding-bottom: 5rem;
}

h3 {
  text-align: center;
}
.portfolio__item {
  background: var(--color-bg-variant);
  padding: 1rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.portfolio__item:hover {
  border-color: var(--color-primary-variant);
  background: transparent;
}

.portfolio__item-image {
  border-radius: 2rem;
  overflow: hidden;
}

.portfolio__item h3 {
  margin: 1rem 0 1.6rem;
}

.portfolio__item-cta {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin: 0.6rem;
  /* align-items: center; */
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 1024px) {
  .portfolio__container {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
  }
}
/*----------------------------------------------------------------*/
@media screen and (max-width: 600px) {
  .portfolio__container {
    grid-template-columns: 1fr;
  }
}

.container.contact_container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  grid-gap: 12%;
  gap: 12%;
}
#contact {
  height: 80vh;
  padding-top: 4rem;
}

.container h5 {
  font-size: 0.9rem;
}

.contact__options {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}
.contact_option {
  background: var(--color-bg-variant);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transition);
}

.contact_option:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
}

.contact_option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.contact_option a {
  text-decoration: underline;
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 1rem;
}

form {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: var(--color-white);
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 1024px) {
  #contact {
    height: 110vh;
  }
  .container.contact_container {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 600px) {
  #contact {
    height: 140vh;
  }
  .container.contact_container {
    width: var(--container-width-sm);
  }
}

footer {
  background-color: var(--color-primary-variant);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a {
  color: var(--color-bg);
}

.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  font-size: 1.2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.footer__socials {
  font-size: 2rem;
  display: flex;
  justify-content: center;
  grid-gap: 3rem;
  gap: 3rem;
  margin-bottom: 4rem;
}

.footer__socials a {
  background: var(--color-bg);
  color: var(--color-white);
  padding: 0.8rem;
  display: flex;
  border: 1px solid transparent;
  border-radius: 0.7rem;
}

.footer__socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}

.footer__copyright {
  font-size: 1.2rem;
  margin-bottom: 4rem;
  color: var(--color-bg);
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    grid-gap: 1.4rem;
    gap: 1.4rem;
  }

  .footer__socials {
    font-size: 1rem;
    margin-bottom: 2.6rem;
  }
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #072e27;
  --color-bg-variant: #0e574b;
  --color-primary: #2d9e8f;
  --color-primary-variant: #047e7c;
  --color-white: #ffff;
  --color-light: rgb(190, 178, 178);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Josefin Sans", sans-serif;
  background-color: #072e27;
  background-color: var(--color-bg);
  color: #ffff;
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(/static/media/bgtex.e3577ddd.png);
}
/* --------------------------------------------------------------------- */
.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 4rem;
}

section {
  margin-top: 8rem;
  /* height: 100vh; */
}
section > h2,
section > h5 {
  text-align: center;
  color: rgb(190, 178, 178);
  color: var(--color-light);
}
scetion > h2 {
  color: #2d9e8f;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgb(190, 178, 178);
  color: var(--color-light);
}

a {
  color: #2d9e8f;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: #ffff;
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #2d9e8f;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #2d9e8f;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn :hover {
  background: #ffff;
  background: var(--color-white);
  color: #072e27;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  background: #2d9e8f;
  background: var(--color-primary);
  color: #072e27;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }

  .section {
    margin-top: 6rem;
  }
}

/*----------------------------------------------------------------*/
@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }

  .section > h2 {
    margin-bottom: 2rem;
  }
}

